import React from 'react';
import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';

import { useTranslation } from 'react-i18next';

function App() {
  const { t, i18n } = useTranslation();
  return (
      <title>Trust i18n</title>,
      <div class="container-fluid">
      <div className="jumbotron ">
        <h1 className="display-4 text-center App-title">
          { t('welcome message', {keyword: 'Trust'}) }
        </h1>
        <p class="lead text-center">
          { t('another message', {salesforce: 'Salesforce'}) }
        </p>

        {/*<hr className="my-4"></hr>*/}

        {/*<p class="text-center">{ t('incident', {count: 2}) }</p>*/}
      </div>
      <br/><br/><br/>
      <div class="container-sm col-2">
          <button type="button" class="btn btn-dark btn-block" onClick={() => i18n.changeLanguage('en')}>English</button>
          <button type="button" class="btn btn-dark btn-block" onClick={() => i18n.changeLanguage('hi')}>Hindi</button>
          <button type="button" class="btn btn-dark btn-block" onClick={() => i18n.changeLanguage('zh')}>Chinese</button>
          <button type="button" class="btn btn-dark btn-block" onClick={() => i18n.changeLanguage('es')}>Spanish</button>
      </div>
      </div>
 )
}

export default App;

